
.layout {
  display: flex;
  flex-direction: column;
  padding: 40px 260px;
}

.postTitle {
  display: flex;
  justify-content: start;
  color: #000000;
  text-align: center;
  font-size: 20px;
  font-weight: 700;
  align-items: center;
}

span {
  color: tomato;
}

.part {
  display: flex;
  justify-content: space-between;
  width: 100%;
  gap: 40px;
  padding: 40px 0px;
  border-radius: 8px;
  border-bottom: 1px solid #E4E4E4;
}

#writer, #password, #title, #addressType, #addressDetail, #youtube {
  height: 48px;
  border-radius: 8px;
  border: 1px solid #D4D3D3;
  padding: 0px 16px;
  color: #000000;
  font-size: 16px;
  font-weight: 400;
}

#addressNum {
  width: 62px;
  height: 48px;
  border-radius: 8px;
  border: 1px solid #D4D3D3;
  padding: 0px 16px;
  align-items: center;
  text-align: center;
  color: #000000;
  font-size: 16px;
  font-weight: 400;
}

#contents {
  border-radius: 8px;
  border: 1px solid #D4D3D3;
  padding: 12px 16px;
  color: #000000;
  font-size: 16px;
  font-weight: 400;
}

.group {
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 8px;
}

.content_part {
  display: flex;
  justify-content: space-between;
  width: 100%;
  gap: 40px;
  padding: 40px 0px;
}

.searchAddress {
  display: flex;
  width: 134px;
  height: 48px;
  border-radius: 8px;
  padding: 12px 16px;
  background-color: #FFFFFF;
  border: 1px solid #000000;
  color: #000000;
  font-size: 18px;
  font-weight: 600;
}

.address {
  display: flex;
  justify-content: space-between;
  padding: 0px 0px 40px;
}

.section {
  display: flex;
  gap: 8px;
  border-radius: 8px;
}

.upload {
  display: flex;
  justify-content: start;
  width: 100%;
  gap: 40px;
  padding: 40px 0px;
  border-top: 1px solid #E4E4E4;
}


.photobox {
  display: flex;
  width: 160px;
  height: 160px;
  color: #777777;
  font-size: 16px;
  font-weight: 400;
  padding: 94px 12px 44px;
  border-radius: 8px;
  border: none;
  background-color: #F2F2F2;
  background-image: url(./add.png);
  background-repeat: no-repeat;
  background-position: 62px 42px;
  cursor: pointer;
}

.input::placeholder {
  color: #ABABAB;
  font-size: 16px;
  font-weight: 400;
}

.photoGroup {
  display: flex;
  gap: 16px;
}

.buttons {
  display: flex;
  width: 100%;
  justify-content: right;
  gap: 16px;
}

.cancel {
  border-radius: 8px;
  border: 1px solid #000000;
  background-color: white;
  font-size: 18px;
  font-weight: 600;
  width: 68px;
  height: 48px;
  padding: 12px 16px;
  color: #000000
}

.regist {
  border-radius: 8px;
  border: none;
  color: #FFFFFF;
  background-color:#2974E5;
  font-size: 18px;
  font-weight: 600;
  width: 95px;
  height: 48px;
  padding: 12px 16px;
}